/* UserLogin.css */

.auth-form-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 40px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  background-color: #fff;
  text-align: center;
}

/* Điều chỉnh kích thước chữ và căn chỉnh cho các trường input */
input[type="number"],
input[type="text"],
select,
textarea {
  width: 50%;
  padding: 15px;
  margin-bottom: 20px;
  font-size: 18px; /* Giảm kích thước chữ về 18px */
  box-sizing: border-box;
  border: 1px solid #ccc; /* Thêm viền 1px và màu sắc cho input */
  border-radius: 4px; /* Bo tròn góc của input */
}

/* Điều chỉnh kích thước chữ cho tiêu đề của các trường */
legend {
  font-size: 24px; /* Giảm kích thước chữ về 24px */
  font-weight: bold;
}

/* Thêm một số căn chỉnh và margin theo nhu cầu */
button {
  background-color: #61dafb;
  color: #fff;
  padding: 15px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 18px; /* Giảm kích thước chữ về 18px */
  font-weight: bold; /* Tô đậm chữ của nút */
  margin-top: 10px; /* Thêm margin-top để tạo khoảng cách giữa nút và form */
}

button:hover {
  background-color: #4fa3d1;
}
